<template>
    <section>
        <h2 class="section-title">Проекты</h2>

        <div class="projects-cards row">
            <div
                class="card-container col-sm-6 col-md-4"
                v-for="card in works"
                :key="card.title"
            >
                <div class="project-card" :data-modal-target="card.modal">
                    <div class="project-card__img">
                        <div class="project-card__spacer"></div>

                        <img :src="card.img" alt="" class="project-card__pic">
                    </div>

                    <div class="project-card__layout">
                        <span class="project-card__description">{{card.title}}</span>
                    </div>
                </div>

                <ProjectsModal :card="card"/>
            </div>
        </div>
    </section>
</template>

<script>
import projectsMixin from '@/mixins/projects.mixin'
import ProjectsModal from '@/components/ProjectsModal'

export default {
    components: {ProjectsModal},
    mixins: [projectsMixin]
}
</script>